@import url(https://fonts.google.com/specimen/Roboto?selection.family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.error {
    color: red;
}

.green {
    color:#1DB954;
}

.green-background {
    background-color: #1DB954;
}

.green-button {
    background-color: #1DB954;
    border: none;
    padding: 10px;
}

.bordered {
    border: 1px solid rgba(0, 0, 0, .125); 
    border-radius: 3px;
    background-clip: border-box;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .125);
  }

.carousel {
    text-align: center;
}

.carousel div {
    margin: 0 auto;
}

.five-percent-padding {
    padding: 5%;
}


.styles_container__2hzRJ {
  background-color: hsl(0, 0%, 21%); 
  text-align: center;
  position: absolute;
  width: 100%;
  color: #ffffff;
  height: 70px;
  min-width: 100%;
}

.styles_brand__Obf4_ {
    letter-spacing: .005em; 
    display: inline;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 0px; /* not being ignored. keep to ensure nav brand is centered */
    font-size: 1.4rem;
  }

.styles_org__9Ercr {
    cursor: pointer;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.styles_logo__3dTzk {
    width: 45px;
    margin: 5px;
}

.styles_nav__1rega {    
    display: flex;
}

.styles_navToggle__2DdAM {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important;
}

.styles_nav-auth__3fqca {
  padding: 0 50px;
}

.styles_navToggle__2DdAM:focus ~ .styles_navToggleLabel__20CMW {
  outline: 3px solid rgba(lightblue, .75);
}

.styles_navToggleLabel__20CMW {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 1em;
  height: 100%;
  display: flex;
  align-items: center;
}

.styles_navToggleLabel__20CMW span,
.styles_navToggleLabel__20CMW span::before,
.styles_navToggleLabel__20CMW span::after {
  display: block;
  background: #ffffff;
  height: 2px;
  width: 2em;
  border-radius: 2px;
  position: relative;
}

.styles_navToggleLabel__20CMW span::before,
.styles_navToggleLabel__20CMW span::after {
  content: '';
  position: absolute;
}

.styles_navToggleLabel__20CMW span::before {
  bottom: 7px;
}

.styles_navToggleLabel__20CMW span::after {
  top: 7px;
}

nav {
  position: absolute;
  text-align: left;
  top: 100%;
  left: 0;
  background: var(--background);
  width: 100%;
  transform: scale(1, 0);
  transform-origin: top;
  transition: transform 400ms ease-in-out;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  margin-bottom: 1em;
  margin-left: 1em;
}

nav a {
  color: #FFF;
  text-decoration: none;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}

nav a:hover {
  color: #FFF;
  text-decoration: none;
}

.styles_navToggle__2DdAM:checked ~ nav {
  transform: scale(1,1);
}

.styles_navToggle__2DdAM:checked ~ nav a {
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

.styles_desktopDropdown__ObP_4 {
    display: none;
}

@media screen and (min-width: 768px) {
    .styles_logo__3dTzk {
        display: inline-block;
        width: 50px;
        margin: 0px;
    }
}

@media screen and (min-width: 1000px) {

  .styles_navToggleLabel__20CMW {
    display: none;
  }

  .styles_container__2hzRJ {
    display: grid;
    grid-template-columns: 1fr auto minmax(600px, 3fr) 1fr;
    min-width: 100%;
  }
  
  .styles_org__9Ercr {
    grid-column: 2 / 3;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 25px;
    margin-left: 0em;
  }

  .styles_brand__Obf4_ {
    font-size: 20px;
  }

  .styles_brand__Obf4_ > a {
    color: #FFFFFF;
    text-decoration: none;
  }
  
  nav {
    
    position: relative;
    text-align: left;
    transition: none;
    transform: scale(1,1);
    background: none;
    top: auto;
    top: initial;
    left: auto;
    left: initial;
    
    grid-column: 3 / 4;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  nav ul {
    display: flex;
    align-items: center;
  }
  
  nav li {
    margin-left: 3em;
    margin-bottom: 0;
  }
  
  nav a {
    color: #ffffff;
    opacity: 1;
    position: relative;
    font-size: 1.05em;
    letter-spacing: .015em;
  }
  
  nav a::before {
    content: '';
    display: block;
    height: 3px;
    background: #ffffff;
    position: absolute;
    top: -.75em;
    left: 0;
    right: 0;
    transform: scale(0, 1);
    transition: transform ease-in-out 250ms;
  }
  
  nav a:hover::before {
    transform: scale(1,1);
  }

  .styles_navAuth__1_5ON {
    padding: 0;
  }

}
.hero-inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.testimonial {
    display: flex;
    flex-direction: column;

}


.testimonial-person-icon {
    margin: 0 auto !important;
}
  

.banner-image {
    width: 100%;
}

.banner-buttons {
    display: flex;
}

@media screen and (min-width: 768px) {
    .banner-image {
        width: 50% !important;
    }

    .hero-inner, .testimonial {
        flex-direction: row;
    }

}


.home-icon-wrapper {
    display: flex;
    justify-content: center;
}
  

@media screen and (min-width: 769px) {
    .hero.is-medium .hero-body {
